<template>
    <div class="information-list">
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input :placeholder="$L('输入标题')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="search-status">
			<el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
            <el-select v-model="messageType" size="small"
                       @change="changeTypeFunc">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.label"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>
        <div class="content">
            <div class="content-box" v-loading="loading"  v-if="tableData && tableData.hasOwnProperty('list')">
                <el-table
						ref="multipleTable"
                        :data="tableData.list"
                        :show-header="false"
                        size="small"
                        style="width: 100%" class="table"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="25"  v-if="editSelect">
                    </el-table-column>
                    <el-table-column
                            align="left"
                            min-width="200px">
                        <div slot-scope="scope"  class="item" @click="getInfoFunc(scope.row.id)">
                            <div class="left" v-if="scope.row.img !== ''">
                                <img :src="scope.row.img" class="img">
                            </div>
                            <div class="right">
                                {{ scope.row.title }}<br>
                                {{ scope.row.time }}<br>
                                <i class="el-icon-view icon"></i>{{ scope.row.clickNum }}
                            </div>
                        </div>
                    </el-table-column>
                    <el-table-column
                            align="right">
                        <div slot-scope="scope"  class="item">
                            <p v-bind:class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</p>
                            <i class="el-icon-edit-outline mainFontColor icon" @click="addMsgFunc(scope.row.id)"></i>
                        </div>
                    </el-table-column>
                    <el-table-column
                            width="20">
                    </el-table-column>
                </el-table>
                <div class="page"  v-if="tableData.allNum>pageSize">
                    <el-pagination
                            small
                            background
                            @current-change="handleCurrentChange"
                            layout="prev, pager, next"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>

            </div>

            <div class="botton-btn" v-if="!editSelect">
                <el-button class="btn" type="primary" @click="addMsgFunc()">{{ $L('发布信息') }}</el-button>
            </div>
            <div class="botton-btn" v-else>
               <div class="left">
                   <el-checkbox v-model="checkAll" @change="handleCheckAllChange(tableData.list)">{{ $L('全选') }}</el-checkbox>
               </div>
                <el-button class="delete" type="primary" :disabled=disabled @click="delMsgFunc()">{{ $L('删除') }}</el-button>
            </div>
        </div>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    export default {
        name: "informationList",
        data () {
            return {
                loading:false,
                searchType: false,
                inputValue:'',
                messageType:0,
                messageTypelist:[
                    {
                        type:0,
                        label:this.$L('全部状态')
                    },
                    {
                        type:2,
                        label:this.$L('未审核')
                    },
                    {
                        type:1,
                        label:this.$L('已审核')
                    },
                ],
                tableData:{},
                currentPage:1,
                pageSize:10,
                editSelect:false,
                checkedInfo:[],
                msgArr:[],
				disabled:true,
				dtCreateStart:'',
				dtCreateEnd:'',
				showTime:false,
				timeArr:[],
				defaultDate:[],
            }
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 执行设置顶部按钮
            this.setupTopBtn();
            this.showSearch(false);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        methods : {
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        text: this.$L('编辑'),
                        size: '14px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.edit();
                        }
                    },
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            //编辑
            edit(){
                this.editSelect = !this.editSelect;
                if(!this.editSelect){
                    this.mobileTopBtn[0].text = this.$L('编辑');
                }else{
                    this.mobileTopBtn[0].text = this.$L('取消');
                }
            },
            handleSelectionChange(val){
				if(val.length > 0){
					this.disabled = false;
				}else{
					this.disabled = true;
				}
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
                console.log(val);
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    type: _self.type,
                    keyword: _self.inputValue,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd

                };
                _self.$api('getReleaseList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            getInfoFunc(id) {
                this.$router.push({name: 'informationInfo', params: {id: id}});
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val) {
                this.type = val;
                this.getData();
            },
            statusFunc(value) {
                let x;
                switch (value) {
                    case 0:
                        x = {text: this.$L('未审核'), color: 'font-red'};
                        break;
                    default:
                        x = {text: this.$L('已审核'), color: 'font-grey'};
                        break;
                }
                return x;
            },
            SearchTimeFunc(time) {
                console.log(time);
                // dtCreateStart   dtCreateEnd
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            addMsgFunc(id) {
                if (id) {
                    this.$router.push({name: 'informationEdit', params: {id: id}});
                } else {
                    this.$router.push({name: 'informationEdit', params: {id:'add'}});
                }
            },
            delMsgFunc(id) {

                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中信息，请勾选信息再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'), _self.$L('请刷新页面重试'), 'error');
                        }
                    });
                    return;
                }

                let groupId;

                if (id) {
                    groupId = id;
                } else {
                    groupId = this.msgArr;
                }

                let arr = {
                    id: groupId,
                };
                console.log(arr);
                _self.$confirm(_self.$L('此操作将永久删除该发布信息, 是否继续?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteReleaseNews', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            },
			handleCheckAllChange(rows){
				if (rows) {
				    rows.forEach(rows => {
				        this.$refs.multipleTable.toggleRowSelection(rows, !this.allSelect)
				    })
				    this.allSelect = !this.allSelect;
				
				    let arr = [];
				    if (this.allSelect) {
				        for (let i in rows){
				            arr[i] = rows[i].id;
				        }
				        this.msgArr = arr;
						this.disabled = false;
				    } else {
				        this.msgArr = arr;
						this.disabled = true;
				    }
				}
			},
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';
	@import '../../../assets/icon/iconfont.css';
    
	
     .information-list{
         position: absolute;
         width: 100%;
         height: 100%;
         line-height: 45px;
         top: 0;
         left: 0;
         border-bottom: 1px solid #E4E7ED;
         .showSearch{
             .search-box{
                 top: 0;
             }
             .main-table{
                 margin-top: 45px;
             }
         }
         .search {
             position: relative;
             width: 100%;
             height: 0;
             line-height: 50px;
             text-align: center;
             overflow: hidden;
             @include diy-animation-show(height);
             border-bottom: 1px solid #F2F6FC;

             &.active {
                 height: 50px;
                 padding: 0 10px;
                 background: #fff;
             }

             .bbx-font {
                 position: absolute;
                 width: 50px;
                 height: 50px;
                 line-height: 50px;
                 text-align: center;
                 top: 0;
                 right: 0;
                 font-size: 20px;
             }
             .search-box{
                 width: calc(100% - 40px);
                 /deep/ .el-input__inner{
                     padding: 0 10px;
                 }
                 /deep/ .el-input-group__append{
                     padding: 0 12px;
                     background: #fff;
                     color: #333;
                     font-size: 16px;
                 }
             }
         }
         .search-status{
			 padding: 0 20px;
             height: 46px;
             border-bottom: 1px solid #F2F6FC;
             background: #fff;
             /deep/ .el-select{
                 margin: 0 10px;
                 width: 100px;
                 vertical-align: top;
             }
             .time-search{
                 margin: 7px 0 7px 10px;
                 width: calc(75% - 10px);
             }
         }
         .content{
             width: inherit;
             background: #eff1f5;
             .content-box{
                 width: inherit;
                 height: inherit;
                 margin: 10px 0;
                 margin-bottom: 65px;
                 .table{
                     background: #fff;
                     padding: 0 10px;
                     font-size: 12px;
                     .item{
                         .left{
                             display: inline-block;
                             width: 50px;
                             vertical-align: top;
                             margin-right: 10px;
                             .img{
                                 width: 50px;
                                 height: 50px;
                                 vertical-align: middle;
                             }
                         }
                         .right {
                             display: inline-block;
                             width: calc(100% - 75px);
                             vertical-align: top;
                             .icon{
                                 font-size: 12px;
                                 margin-right: 5px;
                             }
                         }
                         .icon{
                             font-size: 24px;
                         }
                     }
                     .title{
                         text-overflow: ellipsis;
                         white-space: nowrap;
                         overflow: hidden;
                     }
                     .font-red {
                         color: #ff0000;
                     }
                     .font-green {
                         color: #00bf00;
                     }
                     .font-grey {
                         color: #999;
                     }
                 }
             }
             .page{
                 background: #fff;
                 padding: 20px 0;
                 text-align: center;
                 margin-bottom: 50px;
                 /deep/ .el-pagination{
                    height: 26px;
                 }
             }
             .botton-btn{
                 position: fixed;
                 bottom: 0;
                 width: 100%;
                 background: #fff;
                 z-index: 100;
                 border-top: 1px solid #F2F6FC;
                 text-align: center;
                 height: 55px;
                 padding-top: 5px;
                 .btn{
                     width: calc(100% - 20px);
                 }
                 .left{
                     float: left;
                     width: calc(100% - 140px);
                     height: 38px;
                     line-height: 38px;
                     text-align: left;
                     padding-left: 25px;
                 }
                 .delete{
                     width: 140px;
                     float: right;
                     margin-top: 3px;
                 }
             }
         }
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>